* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    zoom: 80%;
    text-transform: uppercase;
}
body {
    margin: 0 5%;
}
nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: fit-content;
    width: 100%;
    border-bottom: 1px solid black;
    img {
        width: 40vw;
    }
    ul {
        list-style: none;
        display: flex;
        align-items: center;
    }
    li {
        padding: 100px;
        font-size: 5vw;
    }
}
#home {
    width: 100%;
    height: 100vh;
    #image {
        background: url(/resources/maxresdefault.jpg);
        background-size: cover;
        background-position: center;
        filter: brightness(50%);
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    h1 {
        color: white;
        font-size: 15vw;
    }
}